import React, { Component } from 'react';
import axios from 'axios';
import {isEmpty} from 'lodash';
import 'bootstrap/dist/css/bootstrap.css';
import { toast } from "react-toastify";

class Comments extends Component {
  constructor() {
    super();
    this.state = {
      dataList: [],
      content: '',
    }
    this.setContent = this.setContent.bind(this);
  }

  componentDidMount() {
    this._getData();
  }
  // componentWillUnmount() { 
  // }
  setContent(e) {
    this.setState({
      content: e.target.value
    });
  }
  _getData = async() => {
    try {
      const res = await axios.get(`/api/posts/comment-list?id=${this.props.postId}`);
      console.log('/api/posts/comment-list', res)
      this.setState({
        dataList: res.data.result
      })
    } catch(e) {
      toast.error('error');
    }
  }
  async _registContent() {
    const now = new Date();
    const params = {
      userId: this.props.userInfo.id,
      postId: this.props.postId,
      content: this.state.content,
      ip: this.props.ip,
      created_at: now,

    }
    await axios.post('/api/posts/comment-regist', params);
    toast.success('.')
    console.log('complete!');
  }

  render() {
    return (
      <div>
          {this.state.dataList.map((d, index) => 
            <div key={index}>
              {d.content}
              <hr />
            </div>
          )}
        <div className="form-floating">
          <textarea className="form-control"
            placeholder="Leave a comment here"
            id="floatingTextarea2"
            style={{height: '100px'}}
            value={this.state.content}
            onChange={this.setContent}
            ></textarea>
          <label for="floatingTextarea2">Comments</label>
        </div>
      </div>
    )
  }
}


export default Comments;